.main-nav {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  outline: 1px solid red;
}

.main-nav__logo-wrapper {
  display: flex;
  align-items: center;
  padding: 15px;
  position: relative;
}

.main-nav__logo { 
  display: flex;
  align-items: center;
  margin-left: 20px;
  font-family: "Robotobold";
  font-size: 13px;
  line-height: 15px;
  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
}

.main-nav__news {
  display: none;
}

.main-nav__donation {  
  cursor: pointer;
}

.main-nav__donation-text {
  display: none;
}

.main-nav__img--logo {
  width: 46px;
}

.main-nav__toggle {
  width: 23px;
  height: 14px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
  outline: 1px solid red;
}

.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 49px;
  left: 12px;
  width: 23px;
  height: 2px;
  background-color: #333333;
}

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--closed .main-nav__toggle {
    background-image: url(../img/icon-mobile-menu.png);
    background-repeat: no-repeat;
    background-size: 23px 14px;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

@media (max-width: $mobile-width-only) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  
  .main-nav--closed .main-nav__user-list {
    display: none;
  }    

  .main-nav--nojs .main-nav__list {
    display: block;
  }
  
  .main-nav--nojs .main-nav__user-list {
    display: block;
  }  
  
}

@media (min-width: $tablet-width) {
  .main-nav {
    width: 700px;
    display: flex;
    flex-wrap: wrap;
  }

  .main-nav__logo-wrapper {
    justify-content: space-between;
    width: 700px;
    margin: 0 auto;
  } 
  
  .main-nav__logo {
    width: 200px;
  }

  .main-nav__list {
    width: 350px;
  }
  
  .main-nav__user-list {
    width: 350px;
  }
  
  .main-nav__search {
    width: 350px;
  }  
}

@media (min-width: $desktop-width) {
  .main-nav {
    position: relative;
    width: 1140px;
    justify-content: space-between;
  }
  
  .main-nav__toggle {
    display: none;
  }
  
  .main-nav__logo-wrapper {
    justify-content: space-between;
    width: 650px;
    margin: 0 auto;
    order: 2;
    outline: 1px solid green;
  } 
  
  .main-nav__news {
    display: block;
    color: #333333;
  }
  
  .main-nav__donation {
    width: 210px;
    display: flex;
    align-items: center;
    background-color: #cc1e40;
    border-radius: 25px;
    color: #ffffff;    
  }
  
  .main-nav__donation-text {
    display: block;
  }
  
  .main-nav__list {  
    width: 1050px;
    order: 3;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }  
  
  .main-nav__user-list {    
    width: 455px;
    order: 1;    
  } 

  .main-nav__user-item {  
    &:first-child {
      position: absolute;
      bottom: 25px;
      right: 0;
    }
  }
  
}
