.information {
  width: 290px;
  margin: 0 auto;
  padding: 0;
  outline: 1px solid red;
  text-align: center;
}

.information__issue {
  padding-bottom: 40px;
  margin-bottom: 30px;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
}

.information__heading {
  position: relative;
  font-family: "Robotobold";
  font-weight: bold;
  text-transform: uppercase;
  color: #333333;
  text-align: left;
  padding-left: 60px;
}

.information__heading::before {
  content: "";
  position: absolute;
  top: 0;
  left: 20px;
  width: 24px;
  height: 24px;  
  background-repeat: no-repeat;
}

.information__heading--cancer::before {
  background-image: url(../img/icon-info.svg);
}

.information__heading--health::before {
  background-image: url(../img/icon-info2.svg);
}

.information__heading--support::before {
  background-image: url(../img/icon-info3.svg);
}

.information__heading--searching::before {
  background-image: url(../img/icon-info4.svg);
}

.information__heading--help::before {
  background-image: url(../img/icon-info5.svg);
}

.information__heading--about::before {
  background-image: url(../img/icon-info6.svg);
}

.information__title {
  font-family: "Robotobold";
  font-weight: bold;
  color: #333333;
  font-size: 26px;
  line-height: 32px;
  text-align: left;
  padding-left: 20px;
}

.information__text {
  text-align: left;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: $tablet-width) {  
  .information {
    width: 700px;
  }
  
  .information__issue {
    display: flex;
  }
  
  .information__issue:nth-child(2n+1) .information__wrapper-text {
    order: -2;
  }
} 

@media (min-width: $desktop-width) {
  .information {
    width: 1140px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
  }
  
  .information__issue {
    padding-bottom: 0;
    box-shadow: none;
    border: 1px solid #ebeaea;
  }
  
  .history__img {
    display: block;
  }
  
  .information__issue--second {    
    width: 750px;
    order: 2;
  }
  
  .information__issue--third {
    float: left;
    width: 358px;
    display: flex;
    flex-direction: column-reverse;
    order: 1;
    padding-bottom: 70px;
  }
  
  .information__issue--fourth {    
    width: 750px;
    order: 3;
    margin-left: auto;
    margin-top: -370px;
    align-self: flex-start;
  }
  
  .information__row {
    width: 1140px;
    order: 3;
    display: flex;
    justify-content: space-between;
  }
  
  .information__issue--fifth {
    width: 750px;
  }
  
  .information__issue--sixth {
    width: 360px;
    flex-direction: column;
  }
}
