.news {
  position: relative;
  width: 290px;
  padding-top: 30px;
  margin-left: auto;
  margin-right: auto;
  outline: 1px solid red;
}

.news__title {
  position: absolute;
  top: -15px;
  left: 0;
  margin: 0;
  padding-left: 10px;
  padding-right: 10px;
  display: inline;
  font-family: "Robotobold";
  font-weight: bold;
  font-size: 11px;
  line-height: 24px; 
  color: #ffffff;
  text-transform: uppercase;
  background-color: #cc1e40;
}

.news__item {
  display: flex;
  margin-bottom: 20px;
}

.news__img {
  margin-right: 20px;
}

.news__name {
  font-family: "Robotobold";
  font-weight: bold;
  font-size: 16px;
  line-height: 24px; 
  color: #333333;
}

@media (min-width: $tablet-width) {
  .news {
    width: 700px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .news__item {
    width: 310px;
    padding-right: 40px;
  }
}

@media (min-width: $desktop-width) {
  .news {  
    width: 1140px;
    margin-top: -60px;
    background-color: #ffffff;
    box-shadow: 0 11px 30px 0px rgba(0, 0, 0, 0.1);
    outline: 1px solid red;
  }
  
  .news__item {  
    width: 250px;
    padding-right: 20px;
    outline: 1px solid green;
  }
}
