.site-list {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}

.site-list__link {
  outline: 1px solid green;
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 40px;
  font-family: "Robotobold";
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;  
  text-transform: uppercase;
  color: #333333;

  &[href]:hover {
    color: rgba(17, 17, 17, 0.6);
  }

  &[href]:active {
    color: rgba(17, 17, 17, 0.3);
  }
}

.site-list__link--active {
  color: #cc1e40;
}

@media (min-width: $desktop-width) {
  .site-list__link {
    padding-left: 0;
    text-align: center;
  }
}
