.user-list {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;  
}

.user-list__item {
  outline: 1px solid green;
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;  
}

.user-list__phone {
  position: relative;
  font-family: "Robotobold";
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  color: #333333;
  padding-left: 50px;
}

.user-list__phone::before {
  position: absolute;
  content: "";
  top: 2px;
  left: 20px;
  width: 14px;
  height: 14px;
  background-image: url(../img/icon-phone.png);
  background-repeat: no-repeat;
}

.user-list__phone-text {
  font-size: 12px;
  line-height: 24px;
  padding: 0;
  margin: 0;
  padding-left: 50px;
  }

.user-list__email {
  position: relative;  
  color: #333333;
  padding-left: 50px;
}

.user-list__email::before {
  position: absolute;
  content: "";
  top: 2px;
  left: 20px;
  width: 17px;
  height: 17px;
  background-image: url(../img/icon-email.svg);
  background-repeat: no-repeat;
}

.user-list__search-link {
  display: block;
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 40px;
  border-radius: 25px;
  cursor: pointer;
  font-size: 16px;
  color: #333333;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
}

.user-list__search-link::before {
  position: absolute;
  content: "";
  top: 2px;
  right: 10px;
  width: 50px;
  height: 50px;
  background-image: url(../img/search.png);
  background-repeat: no-repeat;
}

.user-list__item--news {
  display: none;
}

@media (min-width: $desktop-width) {
  .user-list {
    display: flex;
    justify-content: space-between;
    align-items: center;    
  }
  
  .user-list__item {
    padding-top: 0;
    padding-bottom: 0;
  }
  
  .user-list__item--phone {
    width: 250px;
    padding-left: 0;
  }
  
  .user-list__item--email {
    width: 200px;
    padding-left: 0;
  }
  
  .user-list__item--search {
    
  }
   
  .user-list__search-text {
    display: none;
  }
  
  .user-list__search-link {
    box-shadow: none;
  }
}
