body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #666666;
  font-size: 14px;
  line-height: 26px;
  font-style: normal;
}

a {
  text-decoration: none;
}

/*
img {
  max-width: 100%;
  height: auto;
}*/

input,
textarea,
button {
  font: inherit;
}

.button {
  display: block;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: "Robotobold";
  font-weight: bold;  
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  border-radius: 25px;  
  cursor: pointer;
}

.clearfix:after {
    content: "";
    display: table;
    clear: both;
}
