.slider {
  padding-top: 100px;
  padding-bottom: 70px;
  text-align: center;
  background-color: #c6c6c6;
  background-image: url(../img/bg-back-mobile.jpg);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  outline: 1px solid red;
}

.slider__title {
  font-family: "Pharogabold";
  font-size: 32px;
  line-height: 40px;
  max-width: 290px;
  color: #ffffff;
  margin: 0 auto;
  margin-bottom: 30px;
}

.slider__link {  
  width: 290px;
  margin: 0 auto;  
  background-color: #ffffff;  
  color: #333333;
}

@media (min-width: $tablet-width) {
  .slider {
    background-image: url(../img/bg-back-tablet.jpg);
  }
  
  .slider__title {
    max-width: 450px;
  }
}

@media (min-width: $desktop-width) {
  .slider {
    background-image: url(../img/slide.jpg);
    padding-bottom: 170px;
  }
  
  .slider__wrapper {
    width: 1140px;
    margin: 0 auto;
    outline: 1px solid red;
  }
  
  .slider__title {
    font-size: 60px;
    line-height: 60px;
    max-width: 550px;
    text-align: left;
    margin: 0;
    margin-bottom: 40px;
  }
  
  .slider__link {
    margin: 0;
    background-color: #1e62ac;
    color: #ffffff;
  }
}
