.history {
  margin-right: auto;
  padding-top: 30px; 
  margin-bottom: 50px;  
  background-image: url(../img/map-mobile.png);
  background-position: 0 100%;
  background-size: 80%;
  background-repeat: no-repeat;
  box-shadow: inset 0 11px 30px 0px rgba(0, 0, 0, 0.1);
}

.history__title {
  width: 290px;
  margin: 0 auto;
  padding: 0;
  font-family: "Robotobold";
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;
  color: #333333;
}

.history__wrapper {
  width: 290px;
  margin: 0 auto;  
}

.history__phone {
  font-family: "Robotobold";
  font-weight: bold;
  font-size: 16px;
  color: #333333;
}

.history__link {
  border: 1px solid #1e62ac;
  color: #1e62ac;
  margin-bottom: 60px;
}

.history__wrapper-img {
  display: flex;
  justify-content: space-between;
}

.history__img-left {
  width: 150px;  
  text-align: right;
  outline: 1px solid green;
}

.history__img-right {
  width: 150px;  
  padding-top: 30px;
  outline: 1px solid green;
}



@media (min-width: $tablet-width) {  
  .history {
    background-image: url(../img/map-tablet.png);
    background-position: 0 30%;
    background-size: 50%;
  }
  
  .history__wrapper {
    width: 700px;
    display: flex;
    justify-content: space-between;
    outline: 1px solid blue;   
  }
  
  .history__wrapper-img {
    order: 1;
    width: 305px;
    padding-top: 80px;
  }
  
  .history__wrapper-text {
    order: 2;
    width: 335px;
  }
  
  .history__link {
    width: 250px;
  }  
}

@media (min-width: $desktop-width) {
  .history {
    background-image: url(../img/map-desktop.png);
    background-position: 0 100%;
    box-shadow: none;
  }
  
  .history__wrapper {
    width: 1140px;
    padding-top: 80px;
  }
  
  .history__wrapper-img {    
    width: 445px;
    padding-left: 40px;
  }
  
  .history__wrapper-text {
    width: 560px;
  }
  
  .history__wrapper-img {
    justify-content: center;
    padding-top: 0;
  }
  
  .history__img-left {
    margin-right: 20px;
    width: 211px;
  }
  
  .history__img-right {    
    width: 220px;
  }
  
  .history__title {
    width: 500px;
    margin: 0;
  }
}
